$primaryColor: #4527a4;

// Columns
@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

.wrpNoProductFound {
	color: red !important;
	text-align: center;
}

// WooCommerce Recent Products
.wp-block-wrp-recent-products {
	.wrpRecentProducts {
		display: grid;

		.wrpProduct {
			position: relative;
		}

		.wrpProductImg {
			margin: 0;
			margin-bottom: 10px;
			overflow: hidden;

			img {
				width: 100%;
				max-width: 100%;
				height: auto;
			}
		}

		.wrpProductDetails {
			padding: 0 10px;
		}

		.productTitle {
			margin: 0;
			margin-bottom: 10px;

			a {
				text-decoration: none;
				color: inherit;
			}
		}

		.productRating {
			margin-bottom: 10px;

			.star-rating {
				font-family: star;
				font-size: .857em;
				line-height: 1.618;
				width: 5.25em;
				height: 1.618em;
				display: inline-block;
				position: relative;
				overflow: hidden;

				&::before,
				span::before {
					content: "\53\53\53\53\53";
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
				}

				&::before {
					opacity: 0.5;
					color: #aaa;
					white-space: nowrap;
				}

				span {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					overflow: hidden;
					padding-top: 1.618em;

					&::before {
						color: inherit;
						white-space: nowrap;
					}
				}
			}
		}

		.productPrice {
			margin: 0;
			margin-bottom: 12px;
		}

		.productAddToCartArea {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 15px;

			a {
				text-decoration: none;
				white-space: initial;
			}

			.button {
				line-height: initial;
				padding: 12px 20px;
			}

			.added_to_cart {
				font-size: 15px;
				font-weight: 600;
				text-transform: uppercase;
			}
		}

		.productOnSale {
			font-size: 15px;
			font-weight: 600;
			width: 45px;
			height: 45px;
			position: absolute;
			top: -5px;
			right: -5px;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
		}

		.screen-reader-text {
			border: 0;
			clip: rect(1px, 1px, 1px, 1px);
			clip-path: inset(50%);
			height: 1px;
			margin: -1px;
			overflow: hidden;
			padding: 0;
			position: absolute;
			width: 1px;
			word-wrap: normal !important;

			&:focus {
				background-color: #eee;
				clip: auto !important;
				clip-path: none;
				color: #444;
				display: block;
				font-size: 1em;
				height: auto;
				left: 5px;
				line-height: normal;
				padding: 15px 23px 14px;
				text-decoration: none;
				top: 5px;
				width: auto;
				z-index: 100000;
				/* Above WP toolbar. */
			}
		}
	}
}